<template>
  <el-row class="layout-header-top layout-center" type="flex" justify="space-between">
    <el-row class="topLogo">
      <router-link to="/">
        <img src="img/author/author.jpg" alt="Mr_fayu" title="Mr_fayu@163.com" style="height: 60px;">
      </router-link>
    </el-row>
    <el-row class="titleText">
          <span><router-link to='/'>消息</router-link></span>
          <span class="line"></span>
          <span><router-link to="/setting">个人中心</router-link></span>
          <span class="line"></span>
          <span><router-link to="/help">帮助</router-link></span>
          <span class="line"></span>
          <el-dropdown @command="handleSetSize" style="cursor: pointer;">
            <span>字体</span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="medium" :disabled="value==='medium'"><span class="font-size--medium">大</span></el-dropdown-item>
              <el-dropdown-item command="small" :disabled="value==='small'"><span class="font-size--small">中</span></el-dropdown-item>
              <el-dropdown-item command="mini" :disabled="value==='mini'"><span class="font-size--mini">小</span></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-row>
  </el-row>
</template>
<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'layout-header-top',
  data () {
    return {

    }
  },
  computed: {
    ...mapState('size', [
      'value'
    ])
  },
  methods: {
    ...mapActions({
      sizeSet: 'size/set',
    }),
    handleSetSize (val) {
      this.$ELEMENT.size = val
      this.sizeSet(val)
      this.$router.push('/refresh')
    },
  }
}
</script>

<style lang="scss">
.layout-header-top {
  height: 60px;
  background-color: #fff;
  padding: 0 15px;
  color: #666666;

  .titleText{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    line-height: 60px;

    span {
      font-size: 12px;
      color: #666666;
      margin-right: 12px;

      a{
        color: #666666;
        text-decoration: none;
        &:hover {
          color: #3097ee;
        }
      }
    }

    .line {
      width: 1px;
      height: 14px;
      background: #cccccc;
    }
  }


  #titleControl .el-dropdown-link{
    display: none;
  }

  .controlBtn{
    display: inline;
  }

}

@media screen and (max-device-width: 400px) {
  .layout-header-top {

    .topLogo{
      display: none;
    }
    .titleText {
      margin: 0 auto;
    }
  }
}
</style>
